.stars {
  display: flex;

  &-star {
    width: 16px;
    height: 16px;
    left: 80px;
    border-radius: 0.5px;
    margin-right: 4px;
    background-image: url('./images/star.svg');
    background-repeat: no-repeat;
    background-position: center center;
  }

  &-star:nth-child(-n + 4) {
    background-image: url('./images/star-active.svg');
  }
}
